import React, { Component } from 'react';
import classes from './SideItem.css';
import Aux from '../../../../hoc/Aux/Aux';
import { connect } from 'react-redux';


class sideItem extends Component {


    state = {
        active: this.props.active
    }

    test = (id) => {
        let items, i;
        items = document.querySelectorAll(".SideItem__SideItem__1m6vB");
        for (i = 0; i < items.length; i++) {
            items[i].className = "SideItem__SideItem__1m6vB";
        }
        document.querySelector("#"+id).className="SideItem__SideItem__1m6vB SideItem__SideItemActive__1MQQc";
        this.props.clicked();
    }

    componentWillReceiveProps() {
        setTimeout(() => {
            if (this.props.selectAll === true) {
                this.setState(() => {
                    return { active: true };
                });
            }
            else if (this.props.selectAll === false) {
                this.setState(() => {
                    return { active: false };
                });
            }
            ;
        }, 10);

    }

    render() {
        let attachedClassesToItem;
        this.state.active
            ? attachedClassesToItem = [classes.SideItem, classes.SideItemActive]
            : attachedClassesToItem = [classes.SideItem];

        let itemToRender = (<Aux>
            <li id={`sideItem_${this.props.itemId}`} className={attachedClassesToItem.join(' ')} onClick={() => this.test(`sideItem_${this.props.itemId}`)} >
                {this.props.name}
            </li >
        </Aux>)

        if (!this.state.active) {
            itemToRender = (<Aux>
                <li id={`sideItem_${this.props.itemId}`} className={attachedClassesToItem.join(' ')} onClick={() => this.test(`sideItem_${this.props.itemId}`)} >
                    {this.props.name}
                </li >
            </Aux>)
        }

        return (

            itemToRender
        );
    }
}




const mapStateToProps = state => {
    return {
        selectAll: state.chartLine.selectAll,
    };
};

const mapDispatchToProps = dispatch => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(sideItem);


