import * as actionTypes from './actionTypes';

export const fetchIngredientsFailed = (id, active) => {
    return {
        type: actionTypes.FETCH_INGREDIENTS_FAILED,
        id: id,
        active: active,
    };
};

export const selectAll = (items) => {
    return {
        type: actionTypes.SELECT_ALL_ITEMS,
        items: items
    };
};

export const selectNone = (items) => {
    return {
        type: actionTypes.SELECT_NONE_ITEMS,
        items: items
    };
};


