import React, { Component } from 'react';
import { connect } from 'react-redux';
import Aux from '../../hoc/Aux/Aux';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import axios from '../../axios-orders';
import classes from './ChartLine.css'
import Chart from 'chart.js'
// 
import * as funnel from 'chartjs-plugin-funnel';



let myChart;


export class chartLine extends Component {

    renderChart = () => {
        let ctx = document.getElementById('myChart').getContext('2d');
        myChart = new Chart(ctx, {
            type: 'funnel',
            data: this.props.chartData[0],
            options: {
                sort: 'desc',
                bezierCurve: true,
                responsive: true,
                hoverMode: 'index',
                stacked: false,
                hover: {
                    animationDuration: 0
                },
                animation: {
                    duration: 0
                },
                legend: {
                    display: false,
                },
                scales: {
                    yAxes: [{
                        display: true,
                        id: 'y-axis-1',
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                        ticks: {
                            beginAtZero: true,
                            fontColor: "white",
                            fontSize: 14
                        },
                    }],
                    xAxes: [{
                        ticks: {
                            beginAtZero: true,
                            fontColor: "white",
                            padding: 16
                        },
                        gridLines: {
                            display: true,
                            color: "#868680"
                        },
                    }],
                },
                tooltips: {
                    // yAlign: 'bottom',
                    mode: 'single',
                    bodySpacing: 6,
                    titleSpacing: 6,
                    footerSpacing: 6,
                    xPadding: 12,
                    yPadding: 12,
                    titleFontSize: 14,
                    bodyFontSize: 14,
                    backgroundColor: "black",
                    footerFontStyle: 'normal',
                    callbacks: {
                        label: function (tooltipItems, data) {

                            return '   ' + tooltipItems.label
                        }
                    }
                },

            }
        });
        console.clear();


    }

    componentDidMount() {
        this.renderChart();
    }

    componentWillReceiveProps() {

        setTimeout(() => {
            myChart.data = this.props.chartData[0];
            myChart.update();
            console.clear();
        }, 100);

    }


    render() {
        return (
            <Aux>
                <div className={classes.ChartLine}>
                    <canvas id="myChart"></canvas>
                </div>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        chartData: state.chartLine.chartData,
        up: state.chartLine.up,
    };
};

const mapDispatchToProps = dispatch => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(chartLine, axios));


