import React from 'react';

import classes from './Toolbar.css';
import NavigationItems from '../NavigationItems/NavigationItems';

const toolbar = (props) => {
    let attachedClassesToText = [classes.ToolbarText];

    return (
        <header className={classes.Toolbar}>
            <nav>
                {/* <p className={attachedClassesToText.join(' ')}>{`${new Date(Date.now())}`}</p> */}
                <p className={attachedClassesToText.join(' ')}>Quantidade acessos por n</p>
                <span className={classes.DesktopOnly} >
                    <NavigationItems />

                </span>
            </nav>
        </header>
    );
};

export default toolbar;


